<template>
  <div class="vote-detail">
    <!-- main -->
    <detail-main :detail="detail" :pageName="'vote'" :isAdmin="true" :statusTxt="statusTxt" />
    <!-- statistics -->
    <router-link class="statistics" :to="`${$route.fullPath}/statistics?not=${count.notJoin}&had=${count.hadJoin}&canremind=${count.canRemind}`">
      <span class="icon"></span>
      <span class="not">未参与:　{{count.notJoin}}人</span>
      <span class="had">已参与:　{{count.hadJoin}}人</span>
      <span class="arrow">></span>
    </router-link>
    <!-- form -->
    <div class="progress">
      <div class="top">
        <span class="icon" :class="{'radio': chartType == 1, 'multi': chartType == 2}"></span>
        <span class="type">{{chartType == 1 ? '单选' : '多选'}}</span>
      </div>
      <ul class="lists">
        <li class="item" v-for="(item, index) in chart" :key="index">
          <p class="title">{{item.title}}</p>
          <div class="bar">
            <span class="sum">得票：{{item.count}}</span>
            <progress class="pro" :value="item.rate" max="100"></progress>
            <span class="per">{{item.rate}}%</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
export default {
  data() {
    return {
      // 状态码
      statusTxt: ['未开始', '正常', '已结束', '暂停'],
      // 投票内容
      detail: {},
      // 参与人数统计
      count: {
        notJoin: 0,
        hadJoin: 0,
        canRemind: 0
      },
      // 统计图数据
      chart: [],
      chartType: '1'
    };
  },
  created() {
    // 获取投票详情
    this._getDetail();
    this._getStatis();
  },
  methods: {
    // 获取投票详情
    _getDetail() {
      service.vote
        .getDetail({ id: this.$route.params.id })
        .then(res => {
          if (res.data.code === 200) {
            this.detail = res.data.data.detail;
            this.count = {
              notJoin: res.data.data.unjoin_mem_count,
              hadJoin: res.data.data.join_mem_count,
              canRemind: res.data.data.detail.canRemind
            };
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 获取统计图数据
    _getStatis() {
      service.vote.statistic({ id: this.$route.params.id }).then(res => {
        if (res.data.code === 200) {
          this.chart = res.data.data.answer;
          this.chartType = res.data.data.type;
        }
      }).catch(err => {
        console.error(err);
      });
    }
  },
  components: {
    DetailMain
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  background-color #f4f4f4
  font-size 0
  .statistics
    display flex
    align-items center
    height 0.88rem
    padding 0 0.2rem
    margin 0.2rem 0
    background-color #fff
    font-size 0.34rem
    .icon
      width 0.4rem
      height 0.4rem
      margin-right 0.2rem
      background url("../../assets/statistic.png") no-repeat
      background-size cover
    .not
      margin-right 0.5rem
    .arrow
      flex-grow 1
      text-align right
      color #aaa
  .progress
    padding 0 0.2rem
    background-color #fff
    .top
      display flex
      align-items center
      height 0.6rem
      .icon
        width 0.4rem
        height 0.4rem
      .icon.radio
        background url("../../assets/radio-check-bg.png") no-repeat
        background-size cover
      .icon.multi
        background url("../../assets/multi-check-bg.png") no-repeat
        background-size cover
      .type
        margin-left 0.2rem
        font-size 0.36rem
    .lists
      .title
        margin 0.1rem 0
        font-size 0.38rem
        word-break break-all
      .bar
        display flex
        align-items center
        padding-bottom 0.15rem
        border-bottom 0.01rem solid #eee
        font-size 0.34rem
        color #777
        .pro
          flex-grow 1
          margin 0 0.15rem
          border 0.01rem solid $color-main
          border-radius 0.3rem
          &::-webkit-progress-inner-element
            border-radius 0.3rem
          &::-webkit-progress-value
            background-color $color-main
            border-radius 0.3rem
          &::-webkit-progress-bar
            background-color #fff
            border-radius 0.3rem
</style>




