import axios from 'axios';
// window.axios = axios;
const HOST = '';
const mathAid = /\/aid\/([0-9]+)(\/|$)/.exec(window.location.href);
const aid = mathAid ? mathAid[1] : '00000000';
const SUFFIX = `/aid/${aid}`;
export default {
  // 公司简介
  company: {
    getContent(opt) {
      return axios.post(HOST + '/weixin/profile/index' + SUFFIX, opt);
    }
  },
  // 产品
  product: {
    // 产品名称
    getName(opt) {
      return axios.post(HOST + '/weixin/product/lists' + SUFFIX, opt);
    },
    // 产品列表
    getList(opt) {
      return axios.post(HOST + '/weixin/product/investLists' + SUFFIX, opt);
    },
    // 产品详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/product/detail' + SUFFIX, opt);
    },
    // 报告详情
    getReport(opt) {
      return axios.post('xxxx', opt);
    },
    // 历史报告
    getHistoryReport(opt) {
      return axios.post(HOST + '/weixin/product/reportLists' + SUFFIX, opt);
    }
  },
  // 新闻
  news: {
    // 新闻列表
    getList() {
      return HOST + '/weixin/news/lists' + SUFFIX;
    },
    // 新闻详情(0、预览；1、正式)
    getDetail() {
      return [
        HOST + '/weixin/news/previewDetail' + SUFFIX,
        HOST + '/weixin/news/detail' + SUFFIX
      ];
    },
    // 发送评论
    sendMsg(opt) {
      return HOST + '/weixin/news/comment' + SUFFIX;
    },
    // 回复评论
    replyComment(opt) {
      return HOST + '/weixin/news/reply' + SUFFIX;
    },
    // 获取回复内容
    getReply(opt) {
      return HOST + '/weixin/news/readReply' + SUFFIX;
    },
    // 获取评论
    getComments(opt) {
      return HOST + '/weixin/news/readComment' + SUFFIX;
    }
  },
  // 通知
  notice: {
    // 通知列表
    getList() {
      return HOST + '/weixin/notice/lists' + SUFFIX;
    },
    // 通知详情(0、预览；1、正式)
    getDetail() {
      return [
        HOST + '/weixin/notice/previewDetail' + SUFFIX,
        HOST + '/weixin/notice/detail' + SUFFIX
      ];
    },
    // 发送评论
    sendMsg() {
      return HOST + '/weixin/notice/comment' + SUFFIX;
    },
    // 回复评论
    replyComment() {
      return HOST + '/weixin/notice/reply' + SUFFIX;
    },
    // 获取回复内容
    getReply() {
      return HOST + '/weixin/notice/readReply' + SUFFIX;
    },
    // 获取评论
    getComments() {
      return HOST + '/weixin/notice/readComment' + SUFFIX;
    }
  },
  // 投票
  vote: {
    // 投票列表
    getList() {
      return HOST + '/weixin/vote/lists' + SUFFIX;
    },
    // 投票详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/vote/detail' + SUFFIX, opt);
    },
    // 获取统计信息
    statistic(opt) {
      return axios.post(HOST + '/weixin/vote/statis' + SUFFIX, opt);
    },
    // 获取已参与人数
    hadJoin(opt) {
      return HOST + '/weixin/vote/getJoinMem' + SUFFIX;
    },
    // 获取未参与人数
    notJoin(opt) {
      return HOST + '/weixin/vote/getUnjoinMem' + SUFFIX;
    },
    // 提交投票
    submit(opt) {
      return axios.post(HOST + '/weixin/vote/answer' + SUFFIX, opt);
    },
    // 一键提醒
    remind(opt) {
      return axios.post(HOST + '/weixin/vote/remind' + SUFFIX, opt);
    }
  },
  // 调研
  survey: {
    // 调研列表
    getList() {
      return HOST + '/weixin/survey/lists' + SUFFIX;
    },
    // 调研详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/survey/detail' + SUFFIX, opt);
    },
    // 提交调研
    submit(opt) {
      return axios.post(HOST + '/weixin/survey/answer' + SUFFIX, opt);
    },
    // 获取已参与人数
    hadJoin(opt) {
      return HOST + '/weixin/survey/getJoinMem' + SUFFIX;
    },
    // 获取未参与人数
    notJoin(opt) {
      return HOST + '/weixin/survey/getUnjoinMem' + SUFFIX;
    },
    // 一键提醒
    remind(opt) {
      return axios.post(HOST + '/weixin/survey/remind' + SUFFIX, opt);
    }
  }
};



