<template>
  <div class="detail-main" :class="[pageName]">
    <p class="title">{{detail.title}}</p>
    <div class="stamp">
      <span class="time">{{detail.create_time}}</span>
      <span class="author">{{detail.author}}</span>
      <span class="status" :status="detail.status">{{statusTxt[detail.status]}}</span>
    </div>
    <p class="tip" v-if="detail.etime">截至日期：{{detail.etime}}</p>
    <p class="tip" v-if="pageName === 'vote'">本投票{{detail.type==2?'每天':''}}只能投{{detail.num}}次</p>
    <div class="detail-content">
      <div class="des" v-html="detail.desc"></div>
      <p class="tip" v-if="!isAdmin">{{tips}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VoteSurveyDetail',
  props: {
    statusTxt: Array,
    detail: Object,
    pageName: String,
    isAdmin: Boolean
  },
  data() {
    return {
      tips: ''
    };
  },
  created() {
    this.statusTip();
  },
  methods: {
    statusTip() {
      console.log(this.detail.status, this.pageName);
      if (this.pageName === 'survey') {
        if (Number(this.detail.status) === 3) {
          this.tips = '您已参与';
        }
      } else if (this.pageName === 'vote') {
        if (Number(this.detail.status) === 4) {
          this.tips = this.detail.msg;
        }
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import "../common/stylus/base"

.detail-main
  padding 0.2rem
  background-color #fff
  box-shadow 0 2px 8px 1px #ddd
  .title
    margin-bottom 0.1rem
    font-size 0.42rem
    text-align justify
    word-break break-all
  .stamp
    display flex
    align-items center
    font-size 0.32rem
    color #777
    .author
      margin-left 0.5rem
    .status
      flex-grow 1
      text-align right
  .tip
    margin-top 0.1rem
    font-size 0.34rem
    color #777
  .detail-content
    padding 0.3rem 0
    font-size 0.36rem
    .des
      word-break break-all
      text-align justify
    .tip
      color $color-main
      text-align center
    .btn
      text-align center
      color #777

.survey
  .status
    &[status="0"]
      color #fd7c14
    &[status="1"]
      color #2dc56d
    &[status="2"]
      color #d62525
    &[status="3"]
      color #b42632
.vote
  .status
    &[status="0"]
      color #fd7c14
    &[status="1"]
      color #2dc56d
    &[status="2"]
      color #b42632
    &[status="3"]
      color #ef9903
    &[status="4"]
      color #b42632
</style>



